<template>
  <div class="detail">
    <div class="detail-content">
      <div class="banner-box">
        <img :src="detailData.Picture"/>
        <span>{{detailData.pic_group_count}}张照片</span>
      </div>
      <div class="car_info">
        <div class="car_info_l">
          <p class="dealer_price" v-text="detailData.market_attribute.dealer_price"></p>
          <p class="official_refer_price" v-text="detailData.market_attribute.official_refer_price"></p>
        </div>
        <div class="car_info_r"><button v-text="detailData.BottomEntranceTitle"></button></div>
      </div>
      <div class="car_info_list">
        <div class="car_years"><div class="car_year_item" v-for="(item,index) in car_years" :key="index" v-text="item"></div></div>
        <div class="car_info_item">
          <p>1.4L/110kW 涡轮增压</p>
          <div class="car_item_info">
            <h3>奥术大师多所大多阿萨德</h3>
            <p>奥术大师sad啊大声道</p>
            <p><span>阿萨德的</span><span>sad按时的按时</span></p>
          </div>
          <button>询问报价</button>
        </div>
      </div>
    </div>
    <div class="bot_button">
      <button><span v-text="detailData.BottomEntranceTitle"></span><p>本地经销商为你报价</p></button>
    </div>
  </div>
</template>
<script>
import { mapState, mapActions } from "vuex";
export default {
  name: "Detail",
  data() {
    return {
      car_years: ["全部", "2017", "2018"]
    };
  },
  computed: {
    ...mapState({
      detailData: state => state.detail.detailData
    })
  },
  methods: {
    ...mapActions({
      getMakeDetailData: "detail/getMakeDetailData"
    })
  },
  mounted() {
    console.log(this.$route.query.SerialID);
    this.SerialID = this.$route.query.SerialID;
    console.log("SerialID...", this.SerialID);
    this.getMakeDetailData(this.SerialID);
  }
};
</script>
<style lang="scss">
.detail {
  width: 100%;
  height: 100%;
  position: relative;
}

.banner-box {
  height: 3.48rem;
  width: 100%;
  position: relative;
  img {
    height: 100%;
    width: 100%;
  }
  span {
    position: absolute;
    right: 0.36rem;
    bottom: 0.36rem;
    background-color: rgba(155, 155, 155, 0.6);
    font-size: 0.26rem;
    color: #fff;
    border-radius: 0.4rem;
    padding: 0.02rem 0.08rem;
  }
}

.car_info {
  width: 100%;
  display: flex;
  align-items: center;
  padding: 0 0.2rem;
  box-sizing: border-box;
  border-bottom: solid 0.16rem #eee;
}
.car_info_l {
  flex: 1;
}
.dealer_price {
  font-size: 0.36rem;
  color: red;
  font-weight: 500;
}
.official_refer_price {
  font-size: 0.26rem;
  color: silver;
}
.car_info_r button {
  width: 3.74rem;
  height: 0.7rem;
  background-color: #00afff;
  border: 0;
  outline: 0 none;
  border-radius: 0.1rem;
  font-size: 0.32rem;
  color: #fff;
  margin-bottom: 0.5rem;
}

.car_info_list {
  width: 100%;
}
.car_years {
  display: flex;
  .car_year_item {
    font-size: 0.32rem;
    padding: 0.2rem;
    box-sizing: border-box;
  }
}
.car_info_item {
  & > p {
    padding: 0 0.2rem;
    height: 0.5rem;
    line-height: 0.5rem;
    font-size: 0.26rem;
    color: #999;
    background: #f4f4f4;
  }
  .car_item_info {
    padding: 0.2rem;
    box-shadow: border-box;
    h3 {
      padding: 0.26rem 0 0.18rem;
      font-size: 0.3rem;
      line-height: 1;
      color: #3d3d3d;
    }
    & > p {
      &:first-child {
        color: #bdbdbd;
        font-size: 0.26rem;
      }
      &:nth-child(2) > span {
        &:first-child {
          text-align: right;
          padding-bottom: 0.1rem;
          font-size: 0.24rem;
          color: #818181;
        }
        &:nth-child(2) {
          font-size: 0.3rem;
          color: #ff2336;
          margin-left: 0.1rem;
        }
      }
    }
  }
}

.bot_button {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  button {
    height: 1rem;
    width: 100%;
    background-color: #00afff;
    color: #fff;
    border: 0;
    outline: 0 none;
    span {
      font-size: 0.32rem;
      margin-top: 0.12rem;
      font-weight: 500;
    }
    p {
      font-size: 0.24rem;
    }
  }
}
</style>



